<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder="ID"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="userlist" highlight-current-row v-loading="listLoading" @selection-change="selsChange">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" width="80">
			</el-table-column>
			<el-table-column prop="id" label="ID" width="100" sortable>
			</el-table-column>
			<el-table-column label="" width="120">
			  <template slot-scope="scope" width="120">
			    <img :src= "configUrl+scope.row.photo[0].imgUrl" width="100" height="100"/>
			  </template>
			</el-table-column>
			<el-table-column prop="name" label="" min-width="100">
			</el-table-column>
			<el-table-column prop="title" label="" min-width="100">
			</el-table-column>
			<el-table-column prop="model" label="" width="120">
			</el-table-column>
			<el-table-column prop="price" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="netWeight" label="" width="100" sortablealign='center'>
			</el-table-column>
			<el-table-column prop="grossWeight" label="" width="100" sortablealign='center'>
			</el-table-column>
			<el-table-column label="" min-width="200" align='center'>
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="ID" prop="id">
					<el-input v-model="editForm.id" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="title">
					<el-input v-model="editForm.title" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="model">
					<el-input v-model="editForm.model"></el-input>
				</el-form-item>
				<el-form-item label="" prop="price">
					<el-input v-model="editForm.price"></el-input>
				</el-form-item>
				<el-form-item label="" prop="netWeight">
					<el-input v-model="editForm.netWeight"></el-input>
				</el-form-item>
				<el-form-item label="" prop="grossWeight">
					<el-input v-model="editForm.grossWeight"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="ID" prop="id">
					<el-input v-model="addForm.id" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="ID" prop="categoryId">
					<el-input v-model="addForm.categoryId" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="title">
					<el-input v-model="addForm.title" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="model">
					<el-input v-model="addForm.model" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="price">
					<el-input v-model="addForm.price" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="marketPrice ">
					<el-input v-model="addForm.marketPrice" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="shortContent">
					<el-input v-model="addForm.shortContent " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="content">
					<el-input v-model="addForm.content " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="metaKeyword">
					<el-input v-model="addForm.metaKeyword " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="metaDescription">
					<el-input v-model="addForm.metaDescription " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="netWeight">
					<el-input v-model="addForm.netWeight " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="grossWeight">
					<el-input v-model="addForm.grossWeight " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="saleTime">
					<el-input v-model="addForm.saleTime " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="ID" prop="merchantId">
					<el-input v-model="addForm.merchantId " auto-complete="off"></el-input>
				</el-form-item>
				<el-form :model="addForm.photo[0]" label-width="80px" >
					<el-form-item label="ID" prop="id">
						<el-input v-model="addForm.photo[0].id" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="imgUrl">
						<el-input v-model="addForm.photo[0].imgUrl" auto-complete="off"></el-input>
					</el-form-item>
					
				</el-form>
				<el-upload
				  class="upload-demo"
				  action="http://api-dev.sgu6.com/photo"
				  :on-preview="handlePreview"
				  :on-remove="handleRemove"
				  multiple
				  :limit="1"
				  :on-exceed="handleExceed"
				  :file-list="fileList">
				  <el-button size="small" type="primary"></el-button>
				  <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
				</el-upload>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	export default {
		data() {
			return {
				fileList: [],
				configUrl: 'http://www.sgu6.com',
				filters: {
					name: ''
				},
				userlist: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
				  "id": "",
				  "categoryId": "",
				  "name": "",
				  "title": "",
				  "model": "",
				  "price": 0,
				  "marketPrice": 0,
				  "shortContent": "",
				  "content": "",
				  "metaKeyword": "",
				  "metaDescription": "",
				  "netWeight": 0,
				  "grossWeight": 0,
				  "saleTime": new Date().toLocaleString(),
				  "merchantId": "",
				  "photo": [
				    {
				      "id": "",
				      "imgUrl": "",
				      "sort": 0,
				      "show": false
				    }
				  ],
				  "new": false,
				  "enable": false,
				  "hot": false
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
				  "id": "",
				  "categoryId": "",
				  "name": "",
				  "title": "",
				  "model": "",
				  "price": 0,
				  "marketPrice": 0,
				  "shortContent": "",
				  "content": "",
				  "metaKeyword": "",
				  "metaDescription": "",
				  "netWeight": 0,
				  "grossWeight": 0,
				  "saleTime": new Date().toLocaleString(),
				  "merchantId": "",
				  "photo": [
				    {
				      "id": "",
				      "imgUrl": "",
				      "sort": 0,
				      "show": false
				    }
				  ],
				  "new": false,
				  "enable": false,
				  "hot": false
				}

			}
		},
		methods: {
			handleRemove(file, fileList) {
	          console.log(file, fileList);
	        },
	        handlePreview(file) {
	          console.log(file);
	        },
	        handleExceed(files, fileList) {
	          this.$message.warning(` 1  ${files.length} `);
	        },
			//
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//
			getUsers() {
				let url='http://api-dev.sgu6.com/goods/list?page='+this.page+'&pageSize=10'
				this.$http.get(url)
				.then((data)=>{
					this.userlist=data.data.list
					this.total=data.data.total
				})
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
				  "id": "",
				  "categoryId": "",
				  "name": "",
				  "title": "",
				  "model": "",
				  "price": 0,
				  "marketPrice": 0,
				  "shortContent": "",
				  "content": "",
				  "metaKeyword": "",
				  "metaDescription": "",
				  "netWeight": 0,
				  "grossWeight": 0,
				  "saleTime": new Date().toLocaleString(),
				  "merchantId": "",
				  "photo": [
				    {
				      "id": "",
				      "imgUrl": "",
				      "sort": 0,
				      "show": false
				    }
				  ],
				  "new": false,
				  "enable": false,
				  "hot": false
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editUser(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		created() {
			this.getUsers();
		}
	}

</script>

<style scoped>
</style>


