<template>
	<section style="font-size: 16px;">
			<el-form :inline="true" :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<!--<el-form-item label="ID" prop="id">
					<el-input v-model="editForm.id" :disabled="true"></el-input>
				</el-form-item>-->
				<el-form-item label="" prop="username">
					<el-input v-model="editForm.username"></el-input>
				</el-form-item>
				<el-form-item label="" prop="realname">
					<el-input v-model="editForm.realname" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="status">
					<el-select v-model="editForm.status" placeholder="">
					    <el-option
					      v-for="item in options2"
					      :key="item.status"
					      :label="item.value"
					      :value="item.status">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="QQ" prop="qq">
					<el-input v-model="editForm.qq" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="phone">
					<el-input v-model="editForm.phone" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="email">
					<el-input v-model="editForm.email" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="buyerLogonId ">
					<el-input v-model="editForm.buyerLogonId " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="wechatId">
					<el-input v-model="editForm.wechatId " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="gender">
					<el-select v-model="editForm.gender" placeholder="">
					    <el-option
					      v-for="item in options"
					      :key="item.status"
					      :label="item.value"
					      :value="item.status">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="percentage ">
					<el-input v-model="editForm.percentage " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="score">
					<el-input v-model="editForm.score" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="birthday">
					<el-date-picker
				      v-model="editForm.birthday"
				      type="date"
				      placeholder="">
				    </el-date-picker>
				</el-form-item>
				<el-form-item label="" prop="accountSum">
					<el-input v-model="editForm.accountSum" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="VIP" prop="vip">
					<el-select v-model="editForm.vip" placeholder="">
					    <el-option
					      v-for="item in options1"
					      :key="item.status"
					      :label="item.value"
					      :value="item.status">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="riceSum ">
					<el-input v-model="editForm.riceSum " auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click="editSubmit()" :loading="editLoading"></el-button>
				<el-button @click="quxiao()"></el-button>
			</div>
		</section>
</template>

<script>
	import {header} from '../../../apis/header.js'
	import config from '../../../config.js'
	export default {
		data() {
			return {
				options:[{'status':0,'value':''},{'status':1,'value':''}],
				options1:[{'status':0,'value':''},{'status':1,'value':''}],
				options2:[{'status':0,'value':''},{'status':1,'value':''}],
				editForm: {},
				editFormRules: { /**/
					loginName: [{
						required: true,
						message: '',
						trigger: 'blur'
					}]
				},
				editLoading: false,
				id:''
			}
		},
		methods: {
			/**/
			getParams () {
		        this.id = this.$route.query.id
			},
			/**/
			handleEdit(id) {
				let url = config.apiUrl+'/member/' + id
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
					.then((data) => {
						console.log(data.data)
						this.editForm = data.data
					})
			},
			editSubmit(){
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					let url=config.apiUrl+'/member'
					this.$http({
						method:'put',
						url:url,
						data:this.editForm,
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then(data=>{
						if(data.data.errCode==undefined){
							this.$alert('', '', {
					          confirmButtonText: '',
					          callback: action => {
					            this.$router.push({
									path: '/memberlist'
								})
					          }
					        });
						}else{
							this.$alert(data.data.errMsg, '', {
					          confirmButtonText: '',
					          callback: action => {
					            this.$router.push({
									
								})
					          }
					        });
						}
					})
				}).catch(() => {
					return
				});
			},
			quxiao(){
				this.$alert('', '', {
				    confirmButtonText: '',
				    callback: action => {
				        this.$router.push({
							path: '/memberlist'
						})
				    }
				});
			}
		},
		computed: {
			
		},
		created(){
            this.handleEdit(this.$route.query.id)
       },
		watch: {
		// ,
		    '$route': 'getParams'
		}
	}
</script>

<style>
</style>


