// 
const config = {

  // 
  online: false,

  // url
	apiUrl: '//admin-dev-api.sgu6.com',

  // 
  wechatUrl: 'http://wechat-dev.sgu6.com',
}

// 
const hostnameConfigMap = {

  /**
   * dev 
   */
  'wechat-dev.sgu6.com': {
    online: false,
    apiUrl: '//api-dev.sgu6.com',
    wechatUrl: 'http://wechat-dev.sgu6.com',
  },

  /**
   * qa 
   */
  'weixin-qa.sgu6.com': {
    online: false,
    apiUrl: '//api-qa.sgu6.com',
    wechatUrl: 'http://weixin-qa.sgu6.com'
  },

  /**
   *  
   */
  'admin.sgu6.com': {
    online: false,
    apiUrl: '//rest-api.sgu6.com',
    wechatUrl: 'http://weixin.sgu6.com'
  },
}

const hostnameConfig = hostnameConfigMap[window.location.hostname]

// config
Object.assign(config, hostnameConfig)

export default config



