<template>
	<section style="font-size: 16px;">
			<el-form :inline="true" :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<!--<el-form-item label="ID" prop="id">
					<el-input v-model="editForm.id" :disabled="true"></el-input>
				</el-form-item>-->
				<el-form-item label="" prop="loginName">
					<el-input v-model="editForm.loginName"></el-input>
				</el-form-item>
				<el-form-item label="" prop="roleId">
					<el-select v-model="editForm.roleId" placeholder="">
					    <el-option
					      v-for="item in options"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="ID" prop="merchantId">
					<!--<el-input v-model="editForm.merchantId " auto-complete="off"></el-input>-->
					<el-select v-model="editForm.merchantId" placeholder="">
					    <el-option
					      v-for="item in options1"
					      :key="item.id"
					      :label="item.name"
					      :value="item.id">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="status">
					<el-select v-model="editForm.status" placeholder="">
					    <el-option
					      v-for="item in options2"
					      :key="item.status"
					      :label="item.value"
					      :value="item.status">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="idNumber">
					<el-input v-model="editForm.idNumber" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="mobile">
					<el-input v-model="editForm.mobile" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="email">
					<el-input v-model="editForm.email" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="ID" prop="brandBusinessId">
					<el-input v-model="editForm.brandBusinessId" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="ID" prop="wechatId">
					<el-input v-model="editForm.wechatId " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="unionId">
					<el-input v-model="editForm.unionId " auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="province">
					<el-input v-model="editForm.province" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="city">
					<el-input v-model="editForm.city" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="address">
					<el-input v-model="editForm.address" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="birthday">
					<!--<el-input v-model="editForm.birthday" auto-complete="off"></el-input>-->
					<el-date-picker
				      v-model="editForm.birthday"
				      type="date"
				      placeholder="">
				    </el-date-picker>
				</el-form-item>
				<el-form-item label="" prop="gender">
					<el-input v-model="editForm.gender==1 ? '' : ''  " auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click="editSubmit()" :loading="editLoading"></el-button>
				<el-button @click="quxiao()"></el-button>
			</div>
		</section>
</template>

<script>
	import {header} from '../../../apis/header.js'
	import config from '../../../config.js'
	export default {
		data() {
			return {
				options:[],
				options1:[],
				options2:[{'status':0,'value':''},{'status':1,'value':''}],
				editForm: {},
				editFormRules: { /**/
					loginName: [{
						required: true,
						message: '',
						trigger: 'blur'
					}]
				},
				editLoading: false,
				id:''
			}
		},
		methods: {
			/**/
			getParams () {
		        this.id = this.$route.query.id
			},
			/**/
			handleEdit(id) {
				let url = config.apiUrl+'/user/' + id
				this.$http({
					method:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
					.then((data) => {
						console.log(data.data)
						this.editForm = data.data
					})
			},
			editSubmit(){
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					let url=config.apiUrl+'/user'
					this.$http({
						method:'put',
						url:url,
						data:this.editForm,
						header:{
							sessionId: header.sessionId,
							signature: header.signature,
							nonce: header.nonce,
							timestamp: header.timestamp,
							requestId: ''}
					})
					.then(data=>{
						if(data.data.errCode==undefined){
							this.$alert('', '', {
					          confirmButtonText: '',
					          callback: action => {
					            this.$router.push({
									path: '/userlist'
								})
					          }
					        });
						}else{
							this.$alert(data.data.errMsg, '', {
					          confirmButtonText: '',
					          callback: action => {
					            this.$router.push({
									
								})
					          }
					        });
						}
					})
				}).catch(() => {
					return
				});
			},
			quxiao(){
				this.$alert('', '', {
				    confirmButtonText: '',
				    callback: action => {
				        this.$router.push({
							path: '/userlist'
						})
				    }
				});
			},
			getoptions(){
				let url=config.apiUrl+'/role/list?page=1&pageSize=100'
				let url1=config.apiUrl
				this.$http({
					mehtod:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
				.then(data=>{
					this.options=data.data.list
				})
				
			},
			getoptions1(){
				let url=config.apiUrl+'/merchant/list?page=1&pageSize=100'
				let url1=config.apiUrl
				this.$http({
					mehtod:'get',
					url:url,
					header:{
						sessionId: header.sessionId,
						signature: header.signature,
						nonce: header.nonce,
						timestamp: header.timestamp,
						requestId: ''}
				})
				.then(data=>{
					this.options1=data.data.list
				})
				
			}
		},
		computed: {
			
		},
		created(){
            this.handleEdit(this.$route.query.id)
            this.getoptions()
            this.getoptions1()
       },
		watch: {
		// ,
		    '$route': 'getParams'
		}
	}
</script>

<style>
</style>


