import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'
import checkLogin from './filter/checkLogin'

//  CommonJS ,  Vue.use() 
Vue.use(VueRouter)

// 
// 
const router = new VueRouter({
mode: 'history',
routes, // 
})

// 
checkLogin(router)

export default router



